.About {
	h1 {
		padding: 30px 50px;
		border-bottom: 1px solid #bbbbbb;
		font: 40px/1 'arial';
	}
	.container {
		.wrap1 {
			position: relative;
			width: 100%;
			&::after {
				content: '';
				display: block;
				clear: both;
			}
			h2 {
				float: left;
				width: 60%;
				font: 60px/70px 'arial';
				margin-right: 5%;
				padding: 0 50px;
			}
			.intro {
				float: right;
				width: 35%;
				padding: 0 50px;
				h3 {
					margin-bottom: 20px;
				}
				p {
					margin-bottom: 50px;
				}
			}
			.move {
				position: absolute;
				bottom: 10%;
				left: 100px;
				width: 40%;
				height: 150px;

				.circle {
					position: relative;
					top: 0;
					left: 70%;
					width: 150px;
					height: 100%;
					background: #000;
					border-radius: 50%;
					animation: move 10s linear infinite;
					a {
						display: flex;
						justify-content: center;
						align-items: center;
						height: 100%;
						font: 13px/1 'arial';
						color: #fff;
						transform: rotate(-30deg);
					}
				}
			}
		}
		.wrap2 {
			position: relative;
			width: 100%;
			padding: 0 50px;
			margin: 80px 0;
			&::after {
				content: '';
				display: block;
				clear: both;
			}
			article {
				width: 100%;
				height: 600px;
				&:hover img {
					filter: grayscale(0);
				}
				h2 {
					position: absolute;
					width: 70%;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					text-align: center;
					font: 65px/1.3 'arial';
					color: #fff;
					z-index: 2;
				}
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					filter: grayscale(1);
					transition: 0.5s;
				}
			}
		}

		.wrap3 {
			position: relative;
			margin-bottom: 100px;
			.inner {
				margin: 0 50px 80px;
				display: flex;
				flex-direction: column;
				font-family: 'arial';
				.sub-title {
					color: #333;
					margin: 30px 0;
					font: 40px/1.3 'arial';
				}
				#service_type {
					position: relative;
					width: 100%;
					margin: 0 auto;
					nav {
						display: flex;
						margin-bottom: 50px;
						box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
						ul {
							li {
								display: flex;
								justify-content: center;
								align-items: center;
								width: 300px;
								height: 85px;
								background: #eee;
								cursor: pointer;
								transform: scale(1);
								transition: 0.5s;
								h3 {
									font: 20px/1 'arial';
									color: #000;
									text-align: center;
									&:hover {
										font: 21px/1 'arial';
									}
								}
								&.on {
									background: #eae4da;
								}
							}
						}
						.tabBox {
							article {
								padding: 45px;
								display: none;
								&.on {
									display: block;
								}
								h3 {
									font: 30px/1 'arial';
									margin-bottom: 20px;
								}
								p {
									font: 20px/1 $main-font;
									margin-bottom: 20px;
									.check {
										margin-right: 10px;
									}
								}
							}
						}
					}
				}
			}
		}
		.wrap4 {
			position: relative;
			padding: 0 50px;
			&::after {
				content: '';
				display: block;
				clear: both;
			}
			h2 {
				width: 75%;
				color: #333;
				font: 40px/1.3 'arial';
				margin-bottom: 30px;
			}
			article {
				position: relative;
				width: calc(100% / 3);
				height: 400px;
				float: left;
				padding-right: 30px;
				margin-bottom: 50px;
				filter: grayscale(1);
				transition: 0.5s;
				&:hover {
					filter: grayscale(0);
					.member-name {
						display: block;
					}
				}
				&:nth-of-type(3n) {
					padding-right: none;
				}
				h3 {
					font: 15px/1.3 'arial';
					word-break: keep-all;
					&:nth-of-type(3) {
						margin-bottom: 50px;
					}
				}
				strong {
					text-decoration: underline;
				}
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
				.member-name {
					display: none;
					position: absolute;
					width: 100%;
					text-align: center;
					bottom: 0;
					left: 0;
					transition: 0.5s;
					font: 20px/60px $kr-font;
					background: rgba(255, 255, 255, 0.5);
					cursor: pointer;
					.envelope {
						margin-left: 10px;
					}
				}

				.member-position {
					font: 15px/1 $kr-font;
					margin-left: 10px;
				}
			}
		}
	}
}

@keyframes move {
	0% {
		left: 70%;
		transform: rotate(0deg);
	}
	50% {
		left: 0%;
		transform: rotate(180deg);
	}
	100% {
		left: 70%;
		transform: rotate(0deg);
	}
}

@media screen and (max-width: $tablet-plus) {
	.About {
		.container {
			width: 100%;
			.wrap1 {
				h2 {
					width: 57%;
					font: 40px/50px 'arial';
					margin: 0 3%;
					padding: 0 30px;
				}
				.intro {
					width: 37%;
					h3 {
						margin-bottom: 10px;
					}
					p {
						margin-bottom: 30px;
					}
				}
			}
			.wrap2 {
				article {
					height: 450px;
					h2 {
						font: 50px/1.3 'arial';
					}
				}
			}
			.wrap4 {
				h2 {
					font: 30px/1.3 'arial';
				}
				article {
					height: 300px;
					margin-bottom: 50px;
				}
			}
		}
	}
}

@media screen and (max-width: $tablet-origin) {
	.About {
		.container {
			.wrap1 {
				padding-bottom: 150px;
				h2 {
					width: 100%;
					font: 40px/1.1 'arial';
					text-align: center;
					margin: 0;
				}
				.intro {
					width: 100%;
					margin-top: 30px;
				}
				.move {
					width: 80%;
					left: 15%;
					bottom: 5%;
					height: 100px;
					.circle {
						width: 100px;
					}
				}
			}
			.wrap2 {
				margin: 50px 0 0 0;
				h2 {
					font: 40px/1.3 'arial';
				}
			}
			.wrap3 .inner {
				.sub-title {
					font: 30px/1.3 'arial';
				}
				#service_type {
					nav {
						ul li {
							width: 130px;
							height: 100px;
							h3 {
								font: 13px/1.5 'arial';
								&:hover {
									font: 15px/1.5 'arial';
								}
							}
						}
						.tabBox {
							article {
								padding: 35px;
								p {
									font: 18px/1.2 $main-font;
								}
							}
						}
					}
				}
			}
			.wrap4 {
				h2 {
					width: 100%;
					font: 30px/1.3 'arial';
				}
				article {
					width: 50%;
				}
			}
		}
	}
}

@media screen and (max-width: $mobile) {
	.About .container {
		.wrap1 {
			h2 {
				margin: 0;
			}
		}
		.wrap2 article h2 {
			font: 40px/1.3 'arial';
		}
		.wrap3 .inner {
			#service_type {
				nav {
					ul li {
						width: 100px;
						h3 {
							font: 11px/1.5 'arial';
							&:hover {
								font: 12px/1.5 'arial';
							}
						}
					}
					.tabBox {
						article {
							padding: 20px;
							p {
								font: 13px/1.2 $main-font;
							}
						}
					}
				}
			}
		}
		.wrap4 {
			text-align: center;
			article {
				width: 100%;
				padding: 0px;
			}
		}
	}
}
